/**************** css ******************/
.loading {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  z-index: 9000;
  background-color: rgba(49, 50, 51, 0.9);
  padding-top: 50%;
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
  line-height: 100%;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-background-clip: padding-box;
  -moz-background-clip: padding-box;
  background-clip: padding-box;
  /*
	&.fade_out {
		@include animation(fade_in .5s ease-out 1 .35s reverse);
		@include opacity(0);

		-webkit-animation: fade_out .5s linear 1 0s reverse; // Safari
		animation: fade_out .5s linear 1 0s reverse;
	}
*/
  /*
	&:after {
		content:'';
		position: absolute;
		left: 50%;
		top: 50%;
		z-index: 100;

		width: $loading_size;
		height: $loading_size;

		margin: {
			top: -($loading_size / 2);
			left: -($loading_size / 2);
		}

		border: $loading_border_size solid transparent;
		border: {
//			top: $loading_border_size solid #fff; //optional for first animation
			left: $loading_border_size solid #fff;
		}

		@include animation(spin .89s linear infinite .25s);

		@include border-radius(50%);

		@include box-sizing(border-box);
		@include background-clip(padding-box);
	}
*/
}
.loading.inline {
    display: inline-block;
    position: relative;
    left: auto;
    right: auto;
    top: auto;
    bottom: auto;
    width: 36px;
    height: 36px;
    background: none;
    margin-right: 2px;
    margin-left: 2px;
    padding: 0px;
    vertical-align: middle;
}
.loading.inline .loading_content {
      padding-top: 12px;
      padding-left: 45px;
}
.loading.small.inline {
    width: 16px;
    height: 16px;
}
.loading.small.inline .loading_content {
      padding-top: 2px;
      padding-left: 23px;
}
.loading.small:before, .loading.small:after {
    width: 16px;
    height: 16px;
    margin-top: -8px;
    margin-left: -8px;
    border-width: 2px;
}
.loading.large.inline {
    width: 64px;
    height: 64px;
}
.loading.large.inline .loading_content {
      padding-top: 26px;
      padding-left: 0px;
      font-size: 10px;
}
.loading.large:before, .loading.large:after {
    width: 64px;
    height: 64px;
    margin-top: -32px;
    margin-left: -32px;
    border-width: 6px;
}
@media (max-width: 479px) {
.loading.responsive.inline {
      width: 64px;
      height: 64px;
}
.loading.responsive:before, .loading.responsive:after {
      width: 64px;
      height: 64px;
      margin-top: -32px;
      margin-left: -32px;
}
}
.loading:before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 100;
    width: 36px;
    height: 36px;
    margin-top: -18px;
    margin-left: -18px;
    border: 4px solid transparent;
    border-top: 4px solid #fff;
    border-bottom: 4px solid #fff;
    -webkit-animation: spin 0.7s linear infinite;
    animation: spin 0.7s linear infinite;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-background-clip: padding-box;
    -moz-background-clip: padding-box;
    background-clip: padding-box;
}
.loading:not(.inline) .loading_content {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 100;
    width: 240px;
    margin-top: 31px;
    margin-left: -120px;
    text-align: center;
}
.loading:not(.inline).small .loading_content {
    margin-top: 18px;
}
.loading:not(.inline).large .loading_content {
    margin-top: -6px;
    font-size: 10px;
}

